import React, { Component } from 'react'
import PropTypes from 'prop-types'

import FormControl from '../../../layout/forms/FormControl'
import FormGroup from '../../../layout/forms/FormGroup'
import Button from '../../../layout/buttons/Button'
import axios from 'axios'
import NotificationManager from 'react-notifications/lib/NotificationManager'
import CSRFToken from '../../CSRFToken'

class AddMeetingAdminForm extends Component {
	static propTypes = {
		addMeeting: PropTypes.func.isRequired,
		doNotWork: PropTypes.bool,
	}

	constructor(props) {
		super(props)

		this.state = {
			do_not_work: props.doNotWork,
			customer: '',
			customer_first_name: '',
			customer_last_name: '',
			customer_phone_number: '',
			customer_fax_number: '',
			barber: '',
			type: '',
		}

		this.onChange = this.onChange.bind(this)
		this.onSubmit = this.onSubmit.bind(this)
		this.loadBarbers = this.loadBarbers.bind(this)
		this.loadCustomers = this.loadCustomers.bind(this)
	}

	onSubmit = (e) => {
		e.preventDefault()

		const phoneRegexValidation = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im

		if (
			!this.state.do_not_work &&
			(!this.state.customer_phone_number.match(phoneRegexValidation) ||
				(this.state.customer_fax_number &&
					!this.state.customer_fax_number.match(
						phoneRegexValidation
					)))
		) {
			NotificationManager.error('Numer telefonu jest niepoprawyny')

			return
		}

		this.props.addMeeting(this.state)
	}

	onChange = (e) => {
		const value =
			e.target.type === 'checkbox' ? e.target.checked : e.target.value

		this.setState({ [e.target.name]: value })
	}

	loadBarbers = async (value) => {
		try {
			const res = await axios.get(
				`${process.env.REACT_APP_API_URL}/accounts/choice-list/barbers/?search=${value}`
			)

			return res.data
		} catch (err) {
			NotificationManager.error(
				'Nie udało się załadować listy fryzjerów',
				'Błąd',
				4000
			)
		}
	}

	loadCustomers = async (value) => {
		try {
			const res = await axios.get(
				`${process.env.REACT_APP_API_URL}/accounts/choice-list/customers/?search=${value}`
			)

			return res.data
		} catch (err) {
			NotificationManager.error(
				'Nie udało się załadować listy klientów',
				'Błąd',
				4000
			)
		}
	}

	render() {
		const {
			do_not_work,
			customer,
			customer_first_name,
			customer_last_name,
			customer_phone_number,
			customer_fax_number,
			barber,
			type,
		} = this.state

		return (
			<form onSubmit={this.onSubmit}>
				<CSRFToken />

				{!this.props.doNotWork ? (
					<>
						<FormControl>
							<FormControl.CheckBoxLabel>
								Nie pracuje
								<FormControl.CheckBox
									name="do_not_work"
									checked={do_not_work}
									onChange={this.onChange}
								/>
							</FormControl.CheckBoxLabel>
						</FormControl>

						<div
							style={{ display: do_not_work ? 'none' : 'block' }}
						>
							<FormGroup>
								<FormControl>
									<FormControl.Label
										htmlFor="customer"
										inputValue={customer}
									>
										Konto Klienta
									</FormControl.Label>

									<FormControl.ChoiceField
										id="customer"
										name="customer"
										value={customer}
										onChange={(_, value) =>
											this.setState({
												customer: value.slug || '',
												customer_first_name:
													value.first_name || '',
												customer_last_name:
													value.last_name || '',
												customer_phone_number:
													value.phone_number || '',
												customer_fax_number:
													value.fax_number || '',
											})
										}
										searchAsync
										defaultOptions={this.props.customerList}
										choices={this.loadCustomers}
									/>
								</FormControl>

								<FormControl>
									<FormControl.Label
										htmlFor="barber"
										inputValue={barber}
									>
										Fryzjer
									</FormControl.Label>

									<FormControl.ChoiceField
										required={!do_not_work}
										id="barber"
										name="barber"
										value={barber}
										onChange={(_, value) =>
											this.setState({
												barber: value,
											})
										}
										searchAsync
										choices={this.loadBarbers}
									/>
								</FormControl>
							</FormGroup>

							<FormGroup>
								<FormControl>
									<FormControl.Label
										htmlFor="customer_first_name"
										inputValue={customer_first_name}
									>
										Imię klienta
									</FormControl.Label>
									<FormControl.Input
										required={!do_not_work}
										type="text"
										id="customer_first_name"
										name="customer_first_name"
										onChange={(e) => {
											if (!customer) this.onChange(e)
										}}
										value={customer_first_name}
										minLength="3"
										maxLength="20"
									/>
								</FormControl>

								<FormControl>
									<FormControl.Label
										htmlFor="customer_last_name"
										inputValue={customer_last_name}
									>
										Nazwisko klienta
									</FormControl.Label>
									<FormControl.Input
										required={!do_not_work}
										type="text"
										id="customer_last_name"
										name="customer_last_name"
										onChange={(e) => {
											if (!customer) this.onChange(e)
										}}
										value={customer_last_name}
										minLength="3"
										maxLength="20"
									/>
								</FormControl>
							</FormGroup>

							<FormGroup>
								<FormControl>
									<FormControl.Label
										htmlFor="customer_phone_number"
										inputValue={customer_phone_number}
									>
										Numer telefonu klienta
									</FormControl.Label>
									<FormControl.Input
										required={!do_not_work}
										type="text"
										id="customer_phone_number"
										name="customer_phone_number"
										onChange={(e) => {
											if (!customer) this.onChange(e)
										}}
										value={customer_phone_number}
										minLength="9"
										maxLength="12"
									/>
								</FormControl>

								<FormControl>
									<FormControl.Label
										htmlFor="customer_fax_number"
										inputValue={customer_fax_number}
									>
										Numer telefonu klienta nr. 2
									</FormControl.Label>
									<FormControl.Input
										type="text"
										id="customer_fax_number"
										name="customer_fax_number"
										onChange={(e) => {
											if (!customer) this.onChange(e)
										}}
										value={customer_fax_number}
										minLength="9"
										maxLength="12"
									/>
								</FormControl>
							</FormGroup>

							<FormControl>
								<FormControl.Label
									htmlFor="type"
									inputValue={type}
								>
									Typ Wizyty
								</FormControl.Label>
								<FormControl.ChoiceField
									required={!do_not_work}
									id="type"
									name="type"
									onChange={(_, value) =>
										this.setState({ type: value })
									}
									value={type}
									choices={[
										{ value: 'hair', label: 'Włosy' },
										{ value: 'beard', label: 'Broda' },
									]}
								/>
							</FormControl>
						</div>
					</>
				) : null}

				{do_not_work ? (
					<>
						<p>
							Czy chcesz zapisać ten okres czasu jako nie
							pracujący?
						</p>
						<Button danger center>
							Tak
						</Button>
					</>
				) : (
					<Button success center>
						Zapisz wizytę
					</Button>
				)}
			</form>
		)
	}
}

export default AddMeetingAdminForm
